.toxinRunner {
    width: 5em;
    height: 5em;
    background: -webkit-gradient(linear, left top, left bottom, from(#6FCF97), to(#66D2EA));
    background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
    border: 2px solid #FFFFFF;
    border-radius: 50%;
    cursor: pointer;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
